import { HIDE_LOADER, SHOW_LOADER } from "../actions/actions";

export const loader = (state = {}, action) => {
  switch (action.type) {
    case HIDE_LOADER:
      return {
        ...state,
        hide: true,
      };

    case SHOW_LOADER:
      return {
        ...state,
        hide: false,
      };
  }
  return state;
};

export default {
  loader,
};
