import { UPDATE_CART, CLEAR_CART } from "../actions/actions";

export const cart = (state = {}, action) => {
  switch (action.type) {
    case UPDATE_CART: {
      const { item } = action.payload;
      let newItems = [...state.items];
      if (!!state.items.find((i) => i.id === item.id)) {
        newItems = state.items.map((currentItem) => {
          if (currentItem.id === item.id) {
            return item;
          } else {
            return currentItem;
          }
        });
      } else {
        newItems.push(item);
      }

      return {
        ...state,
        items: newItems.filter((i) => i.quantity > 0),
      };
    }

    case CLEAR_CART: {
      return {
        ...state,
        items: [],
      };
    }
    default:
      return state;
  }
};

export default {
  cart,
};
